import request from "@/utils/request";
import qs from "qs";

export default {
  applyToJob(postid) {
    return request({
      url: "/application/apply-job/" + postid,
      method: "put"
    });
  },
  checkIfApplied(postid) {
    return request({
      url: "/application/check-if-applied/" + postid,
      method: "get"
    });
  }
};
